<template>
  <div class="back-overlay" :class="!noOverlay ? 'overlay' : 'no-overlay'">
    <div id="modal" class="modal">
      <div class="container-modal">
        <div class="header-modal">
          <slot name="header">&nbsp;</slot>
          <img class="close-btn" src="@/assets/images/close.png" alt="" @click="closeModal">
        </div>
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Modal",
  props: {
    noOverlay: {
      type: Boolean
    },
    closeOverlay: {
      type: Boolean
    }
  },
  data() {
    return {}
  },
  methods: {
    closeModal() {
      this.$emit('closeModal')
    },
  }
}
</script>

<style lang="scss" scoped>

.close-btn {
  cursor: pointer;
}

.header-modal {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 700;
  font-size: 18px;
  margin-bottom: 2.75rem;
}

.container-modal {
  padding: 2.75rem;
}

.modal {
  position: fixed;
  top: 50%;
  right: 50%;
  transform: translate(35%, -30%);
  width: 888px;
  height: 652px;
  background-color: white;
  opacity: 1;
  z-index: 10;
}

.back-overlay {
  position: fixed;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  width: 200vw;
  height: 200vh;
  display: flex;
  justify-content: center;
  z-index: 1;
}

.no-overlay {
  background-color: #00000000;
}

.overlay {
  background-color: #000000ba;
}

</style>